import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { fetchPreprocessedData } from './api'; // Import the correct function

function DataVisualization() {
    const [chartData, setChartData] = useState({});
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                // Fetch the latest preprocessed data
                const response = await fetch('/api/preprocess', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ filename: 'latest' }) // Assuming 'latest' is a placeholder for the actual latest file name
                });

                if (!response.ok) throw new Error('Data fetch failed');

                const data = await response.json();
                setChartData({
                    labels: data.labels,
                    datasets: [{
                        label: 'Dataset',
                        data: data.values,
                        fill: false,
                        borderColor: 'rgb(75, 192, 192)',
                        tension: 0.1
                    }]
                });
                setLoading(false);
            } catch (error) {
                console.error('Error fetching data:', error);
                setLoading(false);
            }
        };

        fetchData();
    }, []);

    return (
        <div className="DataVisualization">
            <div className="section-title">Data Visualization</div>
            {loading ? (
                <p>Loading data...</p>
            ) : Object.keys(chartData).length ? (
                <Line data={chartData} />
            ) : (
                <p>No data available for visualization. Please upload and preprocess data.</p>
            )}
        </div>
    );
}

export default DataVisualization;
